/*------------------------------------*\
	#CHECKOUT VALIDATION
\*------------------------------------*/

.form-row .woocommerce-error.woocommerce-error:not( .invalid-country-not-allowed ) { /* Double selector to augment specificity */
	display: none;
}

/* Double selectors to augment specificity */
.woocommerce-invalid-required-field .woocommerce-error.woocommerce-error.invalid-required-field,
.woocommerce-invalid-email .woocommerce-error.woocommerce-error.invalid-email,
.woocommerce-invalid-confirmation-field .woocommerce-error.woocommerce-error.invalid-confirmation-field {
	display: block;
}
